.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    margin:auto;
  }
  
  .nav-menu{
      display: flex;
      align-items: right;
      color:rgb(255,166,0);
      padding: 60px;
  
  }
  
  .nav-menu li{
      padding: 0 1rem;
  }
  .nav-menu li a{
  font-size: 1.2rem;
  font-weight: 500;
  
  }
  .nav-menu li:hover{
    padding: 0 1rem;
    background-color: #b4c3ff;
    border-radius: 40px;
  }